import ValidCode from '../../../components/validCode.vue'
import { getCode, phoneLogin, getTokenData,checkCode, updatePwd, passwordLogin} from '../../../api/index.js'
import { mapMutations } from 'vuex';

export default {
	components:{
		ValidCode
	},
	data(){
		return {
			step:1, // 1 获取手机哈 2获取验证码 3用户和密码登录
			isPhoneValid:false,
			isCodeValid:false,
			isPwdValid:false,
			phone:undefined,
			cfmCode:undefined,
			maxlength:6,
			seconds:59,
			password:undefined,
			pwdType:'password',
			onSend:false,
			title:'欢迎登录小红心',
		}
	},
	watch:{
		step(val){
			switch(val){
				case 1:
				this.title = '欢迎登录小红心'
				break
				case 2:
				this.title = '输入验证码'
				break 
				case 3:
				this.title = '欢迎登录小红心'
				break
				case 4:
				this.title = '输入手机号'
				break
				case 5:
				this.title = '输入验证码'
				break
				case 6:
				this.title = '设置密码'
				break
				default:
				break
				
			}
		}
	},
	onShow() {
		this.regPhone()
		this.regPwd()
	},
	onLoad(e) {
		console.log(e);
		this.step = e.step ? e.step : 1
	},
	methods:{
		...mapMutations(['MessageBox']),
		goStep(v){
			this.step = v
			this.regPhone()
			this.regPwd()
		},
		changPwdType(){
			this.pwdType = this.pwdType=='password' ? 'text' : 'password' 
		},
		clearPhone(){
			this.phone = undefined
			this.regPhone()
		},
		regPhone(e){
			let val = e && e.detail.value || this.phone
			let reg = /^1[3456789]\d{9}$/
			if(reg.test(val)){
					this.isPhoneValid = true
			}else{
				this.isPhoneValid = false
			}
		},
		regPwd(e){
			let val = e&&e.detail.value || e&&e.target.value || this.password
			if(val && val!=''){
				this.isPwdValid = true
			}else{
				this.isPwdValid = false
			}
		},
		regCode(finish,v){
			// console.log(finish,v);
			this.isCodeValid = finish ? true : false
			this.cfmCode = v
		},
		// 获取验证码
		getCode(){
			if(!this.isPhoneValid || this.onSend) return
			//手机号码登录 type 1
			if(this.step==1){
				this.goStep(2)
				this.type = 1
			}
			//设置密码 type 2
			if(this.step==4){
				this.goStep(5)
				this.type = 2
			}
			
			let timer = setInterval(()=>{
				if(this.seconds==0){
					clearInterval(timer)
					this.onSend = false
					return
				}
				this.seconds--
			},1000)
			//获取电话号码 todo
			getCode({
				phone:this.phone,
				type:this.type
			}).then(res=>{
				uni.showToast({
					title:res.message
				})
			})
		},
		// 通过code登录
		loginByCode(){
			if(!this.isCodeValid) return
			phoneLogin({
				phone:this.phone,
				code:this.cfmCode
			}).then(res=>{
				if(res.code==1){
					this.loginSucces(res.data)
				}
			})
		},
		// 通过密码登录
		changeLoginMethodByPwd(){
			this.goStep(3);
			this.MessageBox({
				title: '温馨提示',
				type: 'HTML',
				msg: '1.第一次登录建议使用验证码登录或微信登录；<br>2.若使用账户密码登录请点击忘记密码，重新设置密码。；',
				buttons: ['确认'],
				reverse: true,
				visible: true
			})		
		},
		loginByPwd(){
			if(!this.isPhoneValid || !this.isPwdValid) return
			// console.log('phone',this.phone)
			// console.log('password',this.password)
			// 1.验证账号密码 2获取token
			passwordLogin({
				phone:this.phone,
				pwd:this.password
			}).then(res=>{
				if(res.code==1){
					this.loginSucces(res.data)
				}
			})
		},
		loginSucces(data){
			console.log('注册登录成功')
			let userInfo = data
			uni.setStorageSync('userInfo',userInfo)
			uni.setStorageSync('userId',userInfo.id)
			
			if(userInfo.forzen==1){
				console.log('您的账号被冻结了')
				uni.showModal({
					title: '提示',
					content: '账号已冻结，请联系客服:400-970-0989',
				})
				this.LoginFail()
				return
			}
			
			console.log('获取用户token')
			this.getToken(userInfo)
		},
		// 设置密码 验证手机号码
		checkCode(){
			if(!this.isCodeValid) return
			checkCode({
				phone:this.phone,
				code:this.cfmCode
			}).then(res=>{
				if(res.code==1){
					console.log(res)
					this.goStep(6)
				}
			})
		},
		// 设置密码
		updatePwd(){
			updatePwd({
				phone:this.phone,
				pwd:this.password
			}).then(res=>{
				if(res.code==1){
					console.log('密码修改成功');
					uni.showToast({
						title: res.message,
						icon:"none"
					});
					// 重新登录
					this.goStep(3)
				}
			})
		},
		LoginFail(){
			uni.removeStorageSync('token')
			uni.removeStorageSync('isLogin')
			this.goHome()
		},
		goHome(){
			uni.switchTab({
			    url: '/pages/Tabbar/index/index'
			});
		},
		getToken(userInfo){
			let form = {
			  grant_type: 'password',
			  username: userInfo.sno + '-shu',
			  password: '123456',
			  client_id: 'smart-heart',
			  client_secret: 'smart-heart-secret'
			}
			getTokenData(form).then(res=>{
				console.log('获取token登录成功======',res.access_token)
				uni.setStorageSync('token',res.access_token)
				uni.setStorageSync('isLogin',true)
				uni.showToast({
					title:'登录成功',
					icon:'none'
				})
				this.goHome()
			})
		},
		
	}
}
